body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.pagination-container {
  display: flex;
  list-style-type: none;
  padding: 10px 10px;
  align-items: baseline;
  justify-content: center;
 }

 .pagination-container .pagination-item {
    padding: 0 12px;
    height: 32px;
    text-align: center;
    margin: auto 4px;
    color: rgba(0, 0, 0, 0.87);
    display: flex;
    box-sizing: border-box;
    align-items: center;
    letter-spacing: 0.01071em;
    border-radius: 16px;
    line-height: 1.43;
    font-size: 13px;
    min-width: 32px;
 }

   .pagination-container .pagination-item.dots:hover {
      background-color: transparent;
      cursor: default;
    }
   .pagination-container .pagination-item:hover {
      background-color: rgba(0, 0, 0, 0.04);
      cursor: pointer;
    }

   .pagination-container .pagination-item.selected {
      background-color: rgba(0, 0, 0, 0.08);
    }


      .pagination-container .arrow::before {
        position: relative;
        content: ' ';

        display: inline-block;
        width: 0.4em;
        height: 0.4em;
        border-right: 0.12em solid rgba(0, 0, 0, 0.87);
        border-top: 0.12em solid rgba(0, 0, 0, 0.87);
      }

      .pagination-container .arrow.left {
        -webkit-transform: rotate(-135deg) translate(-50%);
                transform: rotate(-135deg) translate(-50%);
      }

      .pagination-container .arrow.right {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
      }

     .pagination-container .arrow.disabled {
        pointer-events: none;
      }

      .pagination-container .disabled  .arrow::before {
        border-right: 0.12em solid rgba(0, 0, 0, 0.43);
        border-top: 0.12em solid rgba(0, 0, 0, 0.43);
      }

      .pagination-container .disabled .disabled:hover {
        background-color: transparent;
        cursor: default;
      }


